import { useEffect } from "react";
import { useRouter } from "next/router";
import { Container } from "@/components/container";

const FaqPage = () => {
  const router = useRouter();

  useEffect(() => {
    if (router.asPath.includes("#")) {
      const elementId = router.asPath.split("#")[1];
      const element = document.getElementById(elementId);

      if (element) {
        setTimeout(() => {
          element.scrollIntoView({ behavior: "smooth", block: "start" });
        }, 100); // Offset uchun 100ms kutish
      }
    }
  }, [router.asPath]);

  return (
    <Container className="pb-[58px]">
      <HomeCrumb paths={[{ name: "Вопросы и ответы" }]} />
      <Section className="py-6 px-0">
        <InfoHeader className="mb-5">
          <InfoTitle>Вопросы и ответы</InfoTitle>
        </InfoHeader>
        <div className="p-6">
          <div id="top" className="mb-[29px]">
            <h5 className="info-title mb-[53px]">Часто задаваемые вопросы</h5>
            {/* links */}
            {faqLinks.map(({ id, name, href }) => (
              <div key={id} className="flex gap-1">
                <p className="info-text">{id}.</p>
                <Link
                  href={href}
                  scroll={false} // Brauzerning default scroll'ini bloklash
                  className="info-text text-cerulean hover:underline underline-offset-2 hoverEffect"
                >
                  {name}
                </Link>
              </div>
            ))}
          </div>
          {/* section */}
          <div className="flex flex-col gap-6">
            {faqData.map((item) => (
              <div key={item.id} id={item.id} className="">
                <h4 className="info-title mb-6">{item.title}</h4>
                <p className="info-text mb-4">{item.desc}</p>
                {item.text && <p className="info-text mb-4">{item.text}</p>}
                {item.text2 && <p className="info-text mb-5">{item.text2}</p>}
                <Link href={"#top"} className="info-text text-cerulean">
                  Вернуться к списку вопросов
                </Link>
              </div>
            ))}
          </div>
        </div>
      </Section>
    </Container>
  );
};

export default FaqPage;
